<template>
    <div>
        <p>test</p>
        {{ users }}
        <el-button @click="tohome()">返回</el-button>

        <edd
        :config = 'Config'
        v-model="Value"
        ></edd>

        {{Value}}
    </div>
</template>

<script>
import ed from './editor'

export default {
    components: {
        'edd': ed
    },
    data() {
        return {
            Value: "",
            users: [],
            Config: {
                toolbar: `styleselect`,
                plugins: `
                `,
                height: 100
            }
        }
    },
    created() {
        this.$ajax.getUserInfo().then(response => {
            this.users = response.data.msg
        })
    },
    methods: {
        tohome() {
            // this.$router.go(-1)
            this.$router.push('/');
        }
    }
}
</script>



